<template>
	<div class="theme-dashboard-view">
		<!-- Dashboard Title -->
		<h1>{{ $t("order_home_title") }}</h1>
		<!-- Search Menu -->
		<div class="my-3 p-3 shadow-sm border">
			<div class="d-flex w-100">
				<span
					class="mx-2 p-2 rounded d-flex align-items-center col-2"
					:class="{ border: ShowFilters, 'bg-light': ShowFilters }"
					@click="
						() => {
							ShowFilters = !ShowFilters;
						}
					"
					style="cursor: pointer"
				>
					<i class="fa fa-filter" aria-hidden="true"></i>
					<span class="mx-2" style="width: 15vh">{{
						$t("inventory_search_bar_filter_button")
					}}</span>
				</span>
				<div class="col-10 d-flex align-items-center px-2">
					<div class="col-md-8 col-sm-12 border d-flex">
						<input
							type="tel"
							placeholder="Enter Ordered Phone Number"
							style="border: none"
							class="p-2 w-100"
							v-model="queryText"
							@keydown.enter="SearchOrder"
						/>
						<button
							class="
								btn
								border
								theme-background-primary
								text-light
								p-2
							"
							@click="SearchOrder"
						>
							{{ $t("inventory_search_bar_button_name") }}
						</button>
					</div>
				</div>
			</div>
			<div
				class="my-2 d-flex"
				:class="{ 'd-flex': ShowFilters, 'd-none': !ShowFilters }"
			>
				<!-- Status -->
				<div class="col-4 p-3">
					<strong>Status</strong>
					<div>
						<select
							class="p-2 my-2"
							style="
								width: 100%;
								height: 100%;
								border: 1px solid lightgrey;
							"
							v-model="queryStatus"
						>
							<option :value="null"></option>
							<option :value="0">Pending</option>
							<option :value="1">Delivering</option>
							<option :value="2">Delivered</option>
						</select>
					</div>
				</div>
				<!-- Payments Status -->
				<div class="col-4 p-3">
					<strong>Payment Status</strong>
					<div>
						<select
							class="p-2 my-2"
							style="
								width: 100%;
								height: 100%;
								border: 1px solid lightgrey;
							"
							v-model="queryPaymentStatus"
						>
							<option :value="null"></option>
							<option :value="true">Paid</option>
							<option :value="false">Not Paid</option>
						</select>
					</div>
				</div>
				<!-- Branch -->
				<div class="col-4 p-3">
					<strong>Branch</strong>
					<div>
						<select
							class="p-2 my-2"
							style="
								width: 100%;
								height: 100%;
								border: 1px solid lightgrey;
							"
							v-model="queryBranch"
						>
							<option :value="null"></option>
							<option :value="0">Nani</option>
							<option :value="1">Almoudi</option>
							<option :value="2">Sara</option>
						</select>
					</div>
				</div>
			</div>
		</div>
		<div class="d-flex justify-content-center">
			<LoadingIndicator :active="loading" />
		</div>
		<div v-show="load_error">
			<div class="justify-content-center text-center text-danger my-4">
				<h4>{{ load_error }}</h4>
			</div>
		</div>
		<!-- {{order_data}} -->
		<div class="m-2 p-3" v-show="!loading">
			<Table
				:rows="order_data"
				:hover="true"
				:contain="[
					'id',
					'status',
					'total_price',
					'created_at',
					'user_phoneNumber',
					'branch',
				]"
				:rowClickFunction="clickOrder"
			>
				<template v-slot:id-title>{{
					$t("order_home_table_id")
				}}</template>
				<template v-slot:status-title>{{
					$t("order_home_table_status")
				}}</template>
				<template v-slot:total_price-title>{{
					$t("order_home_table_total_price")
				}}</template>
				<template v-slot:user_phoneNumber-title>{{
					$t("order_home_table_phone_number")
				}}</template>
				<template v-slot:created_at-title>{{
					$t("order_home_table_created_at")
				}}</template>
				<template v-slot:branch-title>
					<!-- TODO: localization -->
					Branch
				</template>
				<template v-slot:branch="params">
					<div>
						<span v-show="params.row.branch == 1">Nani</span>
						<span v-show="params.row.branch == 2">Almoudi</span>
						<span v-show="params.row.branch == 3">Sara</span>
					</div>
				</template>

				<template v-slot:status="params">
					<div>
						<span
							style="border-radius: 5px"
							class="p-1 bg-warning text-light"
							v-show="params.row.status == 0"
							>{{ $t("order_home_table_status_pending") }}</span
						>
						<span
							style="border-radius: 5px"
							class="p-1 bg-primary text-light"
							v-show="params.row.status == 1"
							>{{
								$t("order_home_table_status_delivering")
							}}</span
						>
						<span
							style="border-radius: 5px"
							class="p-1 bg-success text-light"
							v-show="params.row.status == 2"
							>{{ $t("order_home_table_status_delivered") }}</span
						>
					</div>
				</template>

				<template v-slot:items="params">
					<div>
						{{ params.row.items.length }}
						Items
					</div>
				</template>
			</Table>
		</div>

		<!-- Page navigator -->
		<div class="d-flex justify-content-center p-2" v-if="!loading">
			<!-- {{ $t("page_navigator_name") }}:
			<div
				class="mx-3"
				v-for="page in pages"
				:key="page"
				:class="page == current_page ? 'font-weight-bold' : ''"
				style="cursor: pointer"
				@click="setOrders(page)"
			>
				{{ page }}
			</div>
			<div style="cursor: pointer" @click="nextPage">
				<i class="fa fa-arrow-right" aria-hidden="true"></i>
			</div> -->
			<PageNav
				:pages="pages"
				:current_page="current_page"
				@click-page="setOrders"
				@next-page="nextPage"
				@prev-page="prevPage"
				@goto-first="gotoFirstPage"
				@goto-last="gotoLastPage"
			/>
		</div>
	</div>
</template>

<script>
import Table from "@/components/Table.vue";
import LoadingIndicator from "@/components/LoadingIndicator.vue";
import PageNav from "@/components/PageNav.vue";

//Services
import OrderService from "@/services/order.js";

export default {
	name: "DashboardOrders",
	components: {
		Table,
		LoadingIndicator,
		PageNav,
	},
	data() {
		return {
			loading: false,
			load_error: "",
			order_data: [],
			current_page: 1,
			pages: 0,

			// Search variables
			ShowFilters: false,
			queryText: "",
			queryStatus: null,
			queryPaymentStatus: null,
			queryBranch: null,
		};
	},
	methods: {
		clickOrder(index) {
			console.log("Click Order:", index);
			this.$router.push("/orders/" + this.order_data[index].id);
		},
		nextPage() {
			if (this.current_page != this.pages) {
				this.setOrders(this.current_page + 1);
			}
		},
		prevPage() {
			if (this.current_page != 1) {
				this.setOrders(this.current_page - 1);
			}
		},
		gotoFirstPage() {
			if (this.current_page != 1) {
				this.setOrders(1);
			}
		},
		gotoLastPage() {
			if (this.current_page != this.pages) {
				this.setOrders(this.pages);
			}
		},
		setOrders(page) {
			this.loading = true;
			this.load_error = "";
			OrderService.getOrders(page).then(
				(response) => {
					console.log(response);
					console.log(
						Object.keys(response.data.data.orders).map(
							(key) => response.data.data.orders[key].order
						)
					);
					this.loading = false;
					this.current_page = page;
					this.order_data = Object.keys(
						response.data.data.orders
					).map((key) => response.data.data.orders[key].order);
					this.pages = response.data.data.total_pages;
				},
				(error) => {
					this.loading = false;
					console.log("FAIL");
					this.load_error =
						(error.response &&
							error.response.data &&
							error.response.data.message) ||
						error.message ||
						error.toString();
				}
			);
		},
		SearchOrder() {
			this.$router.push({
				name: "OrderSearch",
				params: {
					query_text: this.queryText,
					query_status: this.queryStatus,
					query_payment_status: this.queryPaymentStatus,
					query_branch: this.queryBranch,
				},
			});
		},
	},
	mounted() {
		this.setOrders(this.current_page);
	},
};
</script>

<style>
.font-weight-bold {
	font-weight: 900;
}
</style>